    row-gap: 10.4rem;
  }
}
.loader__icon-wrap {
  position: relative;
  animation: wheelBounce 1.6s infinite;
  transform-origin: center;
}
.loader__icon-wrap::after {
  content: "";
  position: absolute;
  top: calc(100% + 1.2rem);
  left: 50%;
  width: 18.8rem;
  height: 5rem;
  background-image: url("./assets/images/components/loader/wheel-shadow.svg");
  background-size: contain;
  background-repeat: no-repeat;
  animation: wheelShadowBounce 1.6s infinite;
}
@media (max-width: 48em) {
  .loader__icon-wrap::after {
    top: calc(100% + 2.4rem);
    width: 21rem;
    height: 9rem;
  }
}
.loader__icon-inner {
  width: 20.7rem;
  transform-origin: center;
  animation: wheelRotate 1.6s infinite;
}
@media (max-width: 48em) {
  .loader__icon-inner {
    width: 20.4rem;
  }
}
.loader__progress {
  user-select: none;
}

@keyframes wheelRotate {
  0% {
    transform: rotate(0);
    animation-timing-function: cubic-bezier(0.17, 0.84, 0.44, 1);
  }
  50% {
    transform: rotate(180deg);
    animation-timing-function: cubic-bezier(0.76, 0.05, 0.86, 0.06);
  }
  100% {
    transform: rotate(360deg);
    animation-timing-function: cubic-bezier(0.17, 0.84, 0.44, 1);
  }
}
@keyframes wheelBounce {
  0% {
    transform: translateY(-4rem);
    animation-timing-function: cubic-bezier(0.76, 0.05, 0.86, 0.06);
  }
  45% {
    transform: translateY(4rem);
    animation-timing-function: cubic-bezier(0.23, 1, 0.32, 1);
  }
  100% {
    transform: translateY(-4rem);
    animation-timing-function: cubic-bezier(0.76, 0.05, 0.86, 0.06);
  }
}
@keyframes wheelShadowBounce {
  0% {
    transform: translateZ(-2rem) translateX(-50%) scale(1.3);
    animation-timing-function: cubic-bezier(0.76, 0.05, 0.86, 0.06);
    opacity: 0.05;
  }
  45% {
    transform: translateZ(0) translateX(-50%);
    animation-timing-function: cubic-bezier(0.23, 1, 0.32, 1);
    opacity: 0.3;
  }
  100% {
    transform: translateZ(-2rem) translateX(-50%) scale(1.3);
    animation-timing-function: cubic-bezier(0.76, 0.05, 0.86, 0.06);
    opacity: 0.05;
  }
}
