n    position: relative;\n\n    @media (max-width: 48em) {\n        overflow: hidden;\n    }\n}\n\nsection {\n    margin-bottom: 20rem;\n}\n\n// safari fix\nsvg {\n    -webkit-transform: translate3d(0, 0, 0);\n}\n\n._desktop-only {\n    @media (max-width: 48em) {\n        display: none;\n    }\n}\n._mobile-only {\n    @media (min-width: 48em) {\n        display: none;\n    }\n}\n\n.three-scroll {\n    &__wrapper {\n        &.swiper-wrapper {\n            flex-direction: column;\n        }\n    }\n\n    @media (min-width: 48em) {\n        position: relative;\n        z-index: 10;\n        margin-bottom: 20rem;\n        height: 100vh;\n        perspective: 1000rem;\n\n        section {\n            position: absolute;\n            display: flex;\n            align-items: center;\n            justify-content: center;\n            width: 100%;\n            height: 100%;\n            transform: translateX(-50%) translateY(-50%);\n            background-color: $bgColor;\n            overflow: hidden;\n        }\n\n        &__swiper {\n            width: 100%;\n            height: 100%;\n        }\n\n        &__wrapper {\n            perspective: 1000rem;\n\n            &.swiper-wrapper {\n                flex-direction: row;\n            }\n        }\n\n        [data-three-slide] {\n            opacity: 0;\n            visibility: hidden;\n            transform: translateZ(80rem);\n            pointer-events: none;\n            transition:\n                transform 0.6s ease,\n                opacity 0.6s ease,\n                visibility 0.6s ease;\n            -ms-overflow-style: none;\n            scrollbar-width: none;\n            &::-webkit-scrollbar {\n                display: none;\n            }\n\n            &._is-prev {\n                transform: translateY(-40%) translateZ(-80rem);\n            }\n\n            &._is-next {\n                transform: translateY(40%) translateZ(80rem);\n            }\n\n            &.swiper-slide-active {\n                opacity: 1;\n                visibility: visible;\n                pointer-events: all;\n                transform: translateZ(0);\n            }\n        }\n    }\n}\n\n// --------------------------------------------------------------------------\n\n// header / footer\n@import './sections/header';\n@import './sections/footer';\n\n// ui\n@import '../ui/styles/ui.scss';\n@import './sections/modals.scss';\n\n// ---------------------------------- libs ----------------------------------\n\n@import './libs/locomotive-scroll';\n\n// --------------------------------------------------------------------------\n\n@import './dev/vzmsk1.scss';\n@import './dev/markusDM.scss';\n@import './dev/ukik0.scss';\n@import './dev/kie6er.scss';\n","@font-face {\n    font-family: 'Druk Wide Cyr';\n    font-weight: 500;\n    src: url('../assets/fonts/DrukWide_medium.woff2') format('woff2');\n}\n\n@font-face {\n    font-family: 'Gilroy';\n    font-weight: 700;\n    src: url('../assets/fonts/Gilroy_bold.woff2') format('woff2');\n}\n\n@font-face {\n    font-family: 'Druk Text Wide Cyr';\n    font-weight: 500;\n    src: url('../assets/fonts/DrukTextWideCyr_medium.woff2') format('woff2');\n}\n","*,\n*::before,\n*::after {\n    box-sizing: border-box;\n}\nhtml {\n    font-family: 'Roboto'; //     \n    font-size: 0.5208335vw; //   1920 0.520835vw === 10px\n    font-style: normal;\n    font-weight: normal;\n    -webkit-animation: bugfix infinite 1s;\n    line-height: 1.2;\n    margin: 0;\n    height: 100%;\n    padding: 0;\n}\n\nbody {\n    font-style: normal;\n    font-weight: normal;\n    -webkit-animation: bugfix infinite 1s;\n    line-height: 1.2;\n    margin: 0;\n    padding: 0;\n    height: 100%;\n    font-size: 1.8rem;\n    color: $white; //      \n    background-color: $bgColor;\n}\n\ninput,\ntextarea {\n    -webkit-animation: bugfix infinite 1s;\n    line-height: inherit;\n    margin: 0;\n    padding: 0;\n    background-color: transparent;\n    border: none;\n    color: inherit;\n}\na {\n  